function check(a,por)
{
   if (por==1){
      if(a==undefined) return false;
      if(a==null) return false;
      if(typeof(a)!="string") return false;
      if(a.substring(0,1)==a.substring(0,1).toUpperCase()) return true;
   }
   if (por==2){
      if(a==undefined) return false;
      if(a==null) return false;
      if(typeof(a)!="string") return false;
      return true;
   }
   if (por==3){
      if(a==undefined) return false;
      if(a==null) return false;
      if(typeof(a)!="number") return false;
      return true;
   }
   if (por==4){
      if(a==undefined) return false;
      if(a==null) return false;
      if(typeof(a)!="string") return false;
      const exp = /@/;
      return exp.test (a);
   }
}



