export function dayforbirthday(dn)
{

   

   var now = dayjs();

   const date1 = dayjs(dn);



   let d1=now.diff(date1, 'year') 


   let date2=date1.add(d1, 'year');
   if (now.diff(date1, 'day')>0 ) {date2=date1.add(d1+1, 'year');}



   return Math.floor(date2.diff(now, 'day')); 
}